@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Montserrat:wght@300&display=swap');

.App {
    text-align: center;
    padding: 2% 5%;
}

.App * {
    box-sizing: border-box;
    margin: 0;
}

.logo {
    margin: 1.5rem 0 3rem 0;
}

h1 {
    font-family: 'Bebas Neue', cursive;
    font-size: 2em;
}

h2,
h3 {
    font-family: 'Montserrat', sans-serif;
    font-size: 1.25em;
}

h3 {
    color: white;
    font-weight: bold;
}

p {
    font-family: 'Montserrat', sans-serif;
}

ul {
    padding: 0;
    list-style: none;
}

label {
    margin-top: 5px 0;
    color: white;
    font-family: 'Montserrat', sans-serif;
}

.display-none {
    display: none;
}

.Alert {
    padding: 3px 0;
}

.showSuggestions {
    display: block;
}

input[type='text'],
input[type='number'] {
    padding: 8px 6px 8px 10px;
    border-radius: 4px;
    outline: none;
    border: 1px solid #ccc;
    font-size: 14px;
    line-height: 20px;
    align-self: center;
    height: 38px;
}

.city {
    width: 250px;
}

.CitySearch {
    display: flex;
    flex-direction: column;
    flex-basis: 300px;
    max-width: 300px;
    margin: auto;
    position: relative;
}

.suggestions {
    width: 250px;
    position: absolute;
    z-index: 1;
    align-self: center;
    margin: 0;
    top: 85px;
    font-size: 14px;
    box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
    text-align: left;
    padding: 8px 6px 8px 10px;
    background-color: #eee;
    cursor: default;
}

.suggestions > li:last-child {
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
    background-color: #bcdcdb;
}

.event {
    padding: 15px;
    margin-bottom: 10px;
    text-align: left;
    border: 1px solid #ccc;
    border-radius: 4px;
    position: relative;
    background-color: white;
}

.event-container p {
    color: black;
}

.event-status {
    background-color: seagreen;
    color: white;
    margin: 3px;
}
.details-btn {
    padding: 5px 8px;
    margin-top: 10px;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
    background-color: black;
    color: #2ee0eb;
    font-family: 'Bebas Neue', cursive;
}

.name {
    font-size: 18px;
    font-weight: 600;
}

.group-name {
    text-transform: uppercase;
    font-size: 14px;
}

.going,
.visibility {
    font-style: italic;
}

.description {
    overflow-x: hidden;
}

.numberOfEvents {
    width: 200px;
    margin: 20px auto;
    text-align: center;
}

.button-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 20px;
    padding: 20px;
    font-family: sans-serif;
}

.data-vis-wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
}
.data-vis-wrapper .recharts-responsive-container {
    min-width: 100%;
    display: flex;
}
.recharts-surface {
    overflow: visible;
}

.login-button {
    color: #494949;
    text-transform: uppercase;
    text-decoration: none;
    background: #ffffff;
    padding: 20px;
    border: 4px solid #494949;
    display: inline-block;
    transition: all 0.4s ease 0s;
    min-width: 10rem;
    margin: 1rem;
}

/* WELCOME SCREEN */
.WelcomeScreen {
    color: white;
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-image: url('img/welcome-screen.jpg');
    background-size: cover;
}

.WelcomeScreen a {
    font-family: 'Montserrat', sans-serif;
    color: white;
}

.WelcomeScreen h1 {
    font-size: 3em;
}

.WelcomeScreen h2 {
    font-weight: bold;
}

.google-btn {
    cursor: pointer;
    width: 200px;
    height: 40px;
    margin: 1rem;
    background-color: #2ee0eb;
}

.google-btn:hover {
    box-shadow: 0 0 6px #4285f4;
}

.google-btn:active {
    background: #1669f2;
}

.google-icon-wrapper {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background-color: #fff;
}

.google-icon {
    position: relative;
    display: flex;
    width: 30px;
    margin: 2px;
}

.btn-text {
    cursor: pointer;
    float: right;
    color: black;
    font-size: 15px;
    letter-spacing: 0.1px;
    font-family: 'Montserrat', sans-serif;
    background-color: #2ee0eb;
    box-shadow: none;
    border-style: none;
    width: 80%;
    height: 100%;
    text-align: center;
    margin: 0;
}

@media only screen and (min-width: 992px) {
    .data-vis-wrapper {
        flex-direction: row;
    }
    .data-vis-wrapper .recharts-responsive-container {
        flex-basis: 50%;
        max-width: 50%;
        min-width: auto;
        margin: 0 auto;
    }
}
